</template>

<script>
    import VueJsonPretty from 'vue-json-pretty'

    export default {
        components: {
            VueJsonPretty
        },
        data(){
            return {
                popupActive: false,
                current: null
            }
        },
        methods: {
            open(data){
                this.current = data;
                this.popupActive = true;
            }
        }
    }
</script>

